#flashcardcontainermul{
    height: 75vh;
    display: grid;
    grid-template-areas:
    "datadivmul flashcardboxmul";
    grid-template-columns: 25% 75%;
    margin: none;
    padding: none;
    text-align: center;
    margin-left: 50px;
    margin-right: 50px;
}

#buttondiv1{
    color: white;
}

#flashcardboxmul{
    background-color: white;
    border: solid 1px grey;
    height: 75vh;
   grid-area: flashcardboxmul;
   display: grid;
   grid-template-rows: auto auto auto;
   grid-template-areas: 
   'flashheadermul'
   'flashcontentmul'
   'bottombar1';
}

#flashheadermul{
    grid-area: flashheadermul;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-areas: 
    "headermatch1 headermatch2 headermatch2 .";
    border-bottom: 1px solid grey;
}

#headermatch1{
    grid-area: headermatch1;
    display: flex;
    align-items: center;
    justify-content: center;
}

#headermatch2{
    grid-area: headermatch2;
    display: flex;
    align-items: center;
    justify-content: center;
}

#flashcontentmul{
    grid-area: flashcontentmul;
    display: grid;
    grid-template-rows: auto auto;
    grid-template-areas: 
    'subjectword'
    'randomwords';
}

#subjectword{
    grid-area: subjectword;
}

#randomwords{
    grid-area: randomwords;
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    grid-template-areas: 
    'ranwords ranwords'
    'ranwords ranwords';
    margin-left: 100px;
    margin-right: 100px;
    gap: 10px;
}

#ranwords{
    grid-area: ranwords;
}

#ranword{
    border: solid 1px rgb(209, 208, 208);
}
#ranword:hover{
    background-color: rgb(180, 180, 180);
    transition: 0.3s;
}


#bottombar1{
    grid-area: bottombar1;
    display: grid;
    grid-template-columns: 25% 50% 25%;
    grid-template-areas: 
    'progressmul navbuttonsmul scoremul';
    border-top: grey 1px solid;
}

#navbuttonsmul{
    grid-area: navbuttonsmul;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#progressmul{
    grid-area: progressmul;
    justify-content: center;
    display: flex;
    align-items: center;
}

#scoremul{
    grid-area: scoremul;
    justify-content: center;
    display: flex;
    align-items: center;
}

#randomwords div h3{
    cursor: pointer;
}

#datadivmul{
    grid-area: datadivmul;
    display: grid;
    height: 75vh;
    overflow: auto;
    grid-template-columns: 100%;
    background-color: white;
    border-left: solid 1px grey;
    color: white;
}



#datadivmul div div {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px dashed grey;
    padding-top: 5px;
    padding-bottom: 5px;
}

#datadivmul div div h4:hover{
    background-color: rgb(238, 238, 238);
}

#datadivmul div div:hover{
    background-color: rgb(238, 238, 238);
}


#datadivmul div div h4{
    color: rgb(88, 88, 88);
}

#wrongwordsmul{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto;
    column-gap: 40px;
   }

   
   .rightwrongmulpair{
       display: flex;
       flex-direction: row;
       align-items: center;
       justify-content: center;
   }
   
   .rightwrongmulpair h3{
       margin: 10px;
   }
   
   #wrongwordmulsection1{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;
   }
   
   
   #wrongwordmulsection2{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;
   }

   #wrongwordmulsection3{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#wrongwordmulsection4{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#finalreset{
    display: flex;
    align-items: center;
    justify-content: center;
}

#flashcardbtn2 {
border: none;
background-color: rgb(76, 189, 241);
}

#flashcardbtn2:hover{
opacity: 0.7;
transition: 0.3s;
}


@media only screen and (max-width:375px) {

    #randomwords{
        margin-left: 50px;
        margin-right: 50px;
       
    }

    #flashcontentmul h1 {
        font-size: 30px;
     }

     #flashcardcontainermul{
        margin-left: 0px;
        margin-right: 0px;
    }

    #flashcardbtn2 {
        font-size: 10px;
    }

    #navbuttonsmul  {
        flex-wrap: wrap;
    }

    #flashcardbtn2  {
        width: 70px;
    }

    #progressmul h6  {
        font-size: 12px;
    }


    #datadivmul  div div h3 {
        font-size: 20px;
    }

    #flashheadermul {
        font-size: 12px;
     }

     #datadivmul  div div h4 {
        font-size: 15px;
    }

}


@media only screen and (max-width:400px)   {

    #headermatch1 p{
        font-size: 12px;
    }

  }

  @media only screen and (max-width:450px)   {

    #ranword h3{
        font-size: 15px;
    }
  }


  @media only screen and (max-width:500px) and (min-width: 450px)   {
    #ranword h3{
        font-size: 20px;
    }

  }


  @media only screen and (max-width:768px) and (min-width: 376px)  {
    /* For mobile phones: */

    #randomwords{
      
        margin-left: 50px;
        margin-right: 50px;
       
    }

    #flashcardbtn2 {
        font-size: 10px;
    }

    #navbuttonsmul {
        flex-wrap: wrap;
    }

    #flashcardbtn2  {
        width: 70px;
    }

    #progressmul h6  {
        font-size: 12px;
    }

    #flashcontentmul h1 {
       font-size: 30px;
    }

    #datadivmul  div div h3 {
        font-size: 20px;
    }

    #flashheadermul {
        font-size: 12px;
     }

     #datadivmul  div div h4 {
        font-size: 15px;
    }
  }

  
  @media only screen and (min-width:769px)  {

    #flashcardbtn2 {
        font-size: 15px;
        width: 90px;
    }

    #navbuttonsmul  {
        flex-wrap: wrap;
    }

    #flashcardcontainermul{
        margin-left: 100px;
        margin-right: 100px;
    }
}


@media only screen and (max-width:1000px) and (min-width: 0px)  {

    #flashcardcontainermul{
        margin-right: 0px;
        margin-left: 0px;
    }
    
  }




