#flashcardcontainer2{
    height: 75vh;
    display: grid;
    grid-template-areas:
    'datadiv2 flashcardboxflash';
    grid-template-columns: 25% 75%;
    margin: none;
    padding: none;
    text-align: center;
    gap: none;
    margin-top: none;
    margin-bottom: none;
    margin-left: 100px;
    margin-right: 100px;
}

#datadiv2{
    grid-area: datadiv2;
    height: 75vh;
    overflow: auto;
    grid-template-columns: 100%;
    background-color: white;
    border-left: solid 1px grey;
    color: white;
}



#datadiv2 div div {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px dashed grey;
    padding-top: 5px;
    padding-bottom: 5px;
}



#datadiv2 div div h4:hover{
    background-color: rgb(238, 238, 238);
}

#datadiv2 div div:hover{
    background-color: rgb(238, 238, 238);
}

#datadiv2 div div h4{
    color: rgb(88, 88, 88);
}


#buttondiv1{
    color: white;
}

#flashcardboxflash{
    background-color: white;
    border: solid 1px grey;
    height: 75vh;
    display: grid;
    grid-template-rows: 120px auto 120px;
    grid-template-areas: 
    'flashheader'
    'flashcontentdiv'
    'bottombar';
}


#flashheader{
    grid-area: flashheader;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-areas: 
    'header1 header2 header2 .';
    border-bottom: grey 1px solid;
    width: 100%;
}

#header1{
  grid-area: header1;
  display: flex;
  align-items: center;
  justify-content: center;
}

#header2{
  grid-area: header2;
  display: flex;
  align-items: center;
  justify-content: center;
}

#flashcontentdiv{
    grid-area: flashcontentdiv;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#bottombar{
    grid-area: bottombar;
    display: grid;
    grid-template-columns: 25% 75%;
    border-top: 1px solid grey;
    grid-template-areas: 
    'progress1 navbuttons1';
}

#navbuttons1{
    grid-area: navbuttons1;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#progress1{
    grid-area: progress1;
    justify-content: center;
    display: flex;
    align-items: center;
}


#datadiv2 div button{
 width: 100%;
}

#flashcontent{
    color: black;
}

#flashcardbtn1{
    width: 110px;
    border: none;
    background-color: rgb(76, 189, 241);
}

#flashcardbtn1:hover{
    opacity: 0.7;
    transition: 0.3s;
}


#adscol{
    width: 100%;
    grid-area: adscol;
}

@media only screen and (max-width:375px) {

    #flashcontentdiv h1 {
        font-size: 30px;
     }

     #flashcardcontainer2{
        margin-left: 0px;
        margin-right: 0px;
    }

    #flashcardbtn1 {
        font-size: 10px;
    }

    #navbuttons1  {
        flex-wrap: wrap;
    }

    #flashcardbtn1  {
        width: 70px;
    }

    #progress1 h6  {
        font-size: 12px;
    }


    #datadiv2  div div h3 {
        font-size: 20px;
    }

    #flashheading {
        font-size: 25px;
     }

     #datadiv2  div div h4 {
        font-size: 15px;
    }

}

@media only screen and (max-width:400px)   {

    #header1 p{
        font-size: 12px;
    }

  }


  @media only screen and (max-width:768px) and (min-width: 376px)  {
    /* For mobile phones: */

    #flashcardcontainer2{
        margin-left: 0px;
        margin-right: 0px;
    }

    #flashcardbtn1 {
        font-size: 10px;
    }

    #navbuttons1  {
        flex-wrap: wrap;
    }

    #flashcardbtn1  {
        width: 70px;
    }

    #progress1 h6  {
        font-size: 12px;
    }

    #flashcontentdiv h1 {
       font-size: 30px;
    }

    #datadiv2  div div h3 {
        font-size: 20px;
    }

    #flashheading {
        font-size: 30px;
     }

     #datadiv2  div div h4 {
        font-size: 15px;
    }
  }

  
  @media only screen and (min-width:769px)  {

    #flashcardbtn1 {
        font-size: 15px;
        width: 90px;
    }

    #navbuttons1  {
        flex-wrap: wrap;
    }

    #flashcardcontainer2{
        margin-left: 100px;
        margin-right: 100px;
    }
}
  

