*{
  outline: none !important;
}

#flashcardcontainerinput{
    height: 75vh;
    display: grid;
    grid-template-areas: 
    "datadivinput1 flashcardboxinput1";
    grid-template-columns: 25% 75%;
    margin: none;
    padding: none;
    text-align: center;
    margin-left: 100px;
    margin-right: 100px;
    position: relative;
    max-height: 100%;
    justify-content: start;
}



#flashcardboxinput1{
    background-color: white;
    border: solid 1px grey;
    height: 75vh;
    grid-area: flashcardboxinput1;
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-areas: 
    'flashheaderinput'
    'flashcontentinput'
    'bottombarinput';
}

#flashheaderinput{
    grid-area: flashheaderinput;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-areas: 
    'headerinput1 headerinput2 headerinput2 .';
    border-bottom: grey 1px solid;
    width: 100%;
}

#headerinput1{
  grid-area: headerinput1;
  display: flex;
  align-items: center;
  justify-content: center;
}

#headerinput2{
  grid-area: headerinput2;
  display: flex;
  align-items: center;
  justify-content: center;
}


#flashcontentinput{
    grid-area: flashcontentinput;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#bottombarinput{
    grid-area: bottombarinput;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-areas: 
    'progressinput navbuttonsin inputscore';
  border-top: 1px solid grey;
}

#inputsection{
    display: flex;
    flex-direction: column;
    position: relative;
}


#progressinput{
grid-area: progressinput;
display: flex;
    align-items: center;
    justify-content: center;
}

#navbuttonsin{
    grid-area: navbuttonsin;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

#inputscore{
    grid-area: inputscore;
    display: flex;
    align-items: center;
    justify-content: center;
}


#datadivinput1{
    display: grid;
    height: 75vh;
    overflow: auto;
    grid-template-columns: 100%;
    grid-area: datadivinput1;
    border-left: solid 1px grey;
    color: white;
}

#datadivinput1 div div {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px dashed grey;
  padding-top: 5px;
  padding-bottom: 5px;
}


#datadivinput1 div div h4:hover{
  background-color: rgb(238, 238, 238);
}

#datadivinput1 div div:hover{
  background-color: rgb(238, 238, 238);
}

#datadivinput1 div div h4{
  color: rgb(88, 88, 88);
}

#wrongwordsinput{
 display: grid;
 grid-template-columns: auto auto auto auto;
 grid-template-rows: auto auto;
 column-gap: 40px;
}

.rightwronginputpair{
    display: flex;
    flex-direction: row;
}

.rightwronginputpair h3{
    margin: 10px;
}

#wrongwordinputsection1{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


#wrongwordinputsection2{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#wrongwordinputsection3{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#wrongwordinputsection4{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#flashcardbtn4{
  width: 110px;
  border: none;
  background-color: rgb(76, 189, 241);
}

#flashcardbtn4:hover{
  opacity: 0.7;
  transition: 0.3s;
}

#inputsubmitbtn{
  border: none;
  background-color: rgb(76, 189, 241);
}

#inputsubmitbtn:hover{
  opacity: 0.7;
  transition: 0.3s;
}

.inputbox1{
  width: 300px;
  height: 50px;
}

#finalresetinput{
  display: flex;
  align-items: center;
  justify-content: center;
}


