  #headerinput2 h1{
    font-size: 12px;
  }


  #flashcontentinput h1 {
      font-size: 30px;
   }

   #flashcardcontainerinput{
      margin-left: 0px;
      margin-right: 0px;
  }

  #flashcardbtn4 {
      font-size: 10px;
  }

  #navbuttonsin  {
      flex-wrap: wrap;
  }

  #flashcardbtn4  {
      width: 70px;
  }

  #progressinput h6  {
      font-size: 12px;
  }


  #datadivinput1  div div h3 {
      font-size: 20px;
  }

  #flashheaderinput {
      font-size: 12px;
   }

   #datadivinput1  div div h4 {
      font-size: 15px;
  }

}

