#flashcardcontainerhang{
    height: 75vh;
    display: grid;
    grid-template-areas: 
    "datadivhang flashcardboxhang";
    grid-template-columns: 25% 75%;
    margin: none;
    padding: none;
    text-align: center;
    margin-right: 100px;
    margin-left: 100px;
}



#flashcardboxhang{
    background-color: white;
    border: solid 1px grey;
    height: 75vh;
    display: grid;
    grid-template-rows: auto auto auto auto;
    grid-template-areas: 
    'hangheader'
    'hangcontent'
    'buttondiv2'
    'bottombarhang';
}


#hangheader{
    grid-area: hangheader;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-areas: 
    'headerhang1 headerhang2 headerhang2 .';
    border-bottom: grey 1px solid;
}

#headerhang1{
    grid-area: headerhang1;
    display: flex;
    align-items: center;
    justify-content: center;
}

#headerhang2{
    grid-area: headerhang2;
    display: flex;
    align-items: center;
    justify-content: center;
}

#hangcontent{
    grid-area: hangcontent;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

#buttondiv2{
    grid-area: buttondiv2;
   display: grid;
   grid-template-areas: 
   "letterbutton1 letterbutton2 letterbutton3"
   "letterbutton4 letterbutton5 letterbutton6";
}



#letterbutton1{
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;
    border-right: 1px solid grey;
    grid-area: letterbutton1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

#letterbutton2{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-area: letterbutton2;
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;
}

#letterbutton3{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-area: letterbutton3;
    border-top: 1px solid grey;
    border-left: 1px solid grey;
    border-bottom: 1px solid grey;
}

#letterbutton4{
    grid-area: letterbutton4;
    border-right: 1px solid grey;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

#letterbutton5{
    grid-area: letterbutton5;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

#letterbutton6{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    grid-area: letterbutton6;
    border-left: 1px solid grey;
}


#bottombarhang{
    grid-area: bottombarhang;
    display: grid;
    grid-template-areas: 
    'progresshang navbuttonshang scorediv';
    border-top: grey 1px solid;
}

#progresshang{
    grid-area: progresshang;
    display: flex;
    align-items: center;
    justify-content: center;
}

#scorediv{
 grid-area: scorediv;
 display: grid;
 grid-template-areas: 
 'livesleft'
 'scorehang';
}

#livesleft{
    grid-area: livesleft;
    display: flex;
    align-items: center;
    justify-content: center;
}

#scorehang{
    grid-area: scorehang;
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbuttonshang{
    grid-area: navbuttonshang;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

#flashcardbtn3{
    margin-right: 5px;
    background-color: rgb(76, 189, 241);
}

#flashcardbtn3:hover{
    background-color: rgb(119, 203, 241);
    transition: 0.3s;
}

#flashcardbtnhang{
    width: 110px;
    border: none;
    background-color: rgb(76, 189, 241);
}

#flashcardbtnhang:hover{
    opacity: 0.7;
    transition: 0.3s;
}





#datadivhang{
    display: grid;
    height: 75vh;
    overflow: auto;
    grid-template-columns: 100%;
    background-color: white;
    border-left: solid 1px grey;
    color: white;
}

#datadivhang div div {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px dashed grey;
    padding-top: 5px;
    padding-bottom: 5px;
}


#datadivhang div div h4{
    color: rgb(88, 88, 88);
}

#datadivhang div div h4:hover{
    background-color: rgb(238, 238, 238);
}

#datadivhang div div:hover{
    background-color: rgb(238, 238, 238);
}


#placeholder{
    letter-spacing: 5px;
}


#wrongwordshang{
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: auto auto;
    column-gap: 40px;
   }
   
   .rightwronghangpair{
       display: flex;
       flex-direction: row;
       align-items: center;
       justify-content: center;
   }
   
   .rightwronghangpair h3{
       margin: 10px;
   }
   
   #wrongwordhangsection1{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;
   }
   
   
   #wrongwordhangsection2{
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;
   }

   #wrongwordhangsection3{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#wrongwordhangsection4{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#finalresethang{
    display: flex;
    align-items: center;
    justify-content: center;
}

@media only screen and (max-width:375px) {

    #hangcontent h1 {
        font-size: 30px;
     }

     #flashcardcontainerhang{
        margin-left: 0px;
        margin-right: 0px;
    }

    #flashcardbtn3 {
        font-size: 10px;
    }

    #navbuttonshang  {
        flex-wrap: wrap;
    }

    #flashcardbtn3  {
        width: 70px;
    }

    #progresshang h6  {
        font-size: 12px;
    }


    #datadivhang  div div h3 {
        font-size: 20px;
    }

    #hangheader {
        font-size: 25px;
     }

     #datadivhang  div div h4 {
        font-size: 15px;
    }

}




@media only screen and (max-width:768px) {
    /* For mobile phones: */
    #flashcardbtn3 {
        font-size: 10px;
    }

    #datadivhang div div h4 {
        font-size: 15px;
    }

    #flashcardbtnhang {
        font-size: 10px;
    }

    #datadivhang div div h4 {
        font-size: 15px;
    }

    #flashcardcontainerhang {
        margin-left: 100px;
        margin-right: 100px;
    }

    #navbuttonshang {
        flex-wrap: wrap;
    }

    #flashcardbtnhang {
        width: 70px;
    }

    #progresshang h6  {
        font-size: 20px;
    }

    #datadivhang  div div h3 {
        font-size: 20px;
    }

    #datadivhang  div div h4 {
        font-size: 15px;
    }

    

    #hangcontent h1 {
        font-size: 30px;
     }

     #hangheader {
        font-size: 30px;
     }

     #letterbuttoncontainer1 {
        flex-wrap: wrap;
     }

     #letterbuttoncontainer2 {
        flex-wrap: wrap;
     }

     #wrongwordshang {
        grid-template-columns: auto auto auto auto;
        column-gap: 0;
     }

     #wrongwordshang h3 {
        font-size: 10px;
     }

  }

  @media only screen and (max-width:825px) and (min-width: 766px)  {
    
    #flashcardbtnhang  {
        font-size: 15px;
        width: 75px;
    }
  }

  @media only screen and (max-width:768px) and (min-width: 350px)  {

    #headerhang1{
