    }

    #headerhang2 h1{
        font-size: 40px;
    }

    #flashcardcontainerhang{
        margin-right: 0px;
        margin-left: 0px;
    }

    #flashcardbtnhang {
        font-size: 10px;
    }

    #navbuttonshang {
        flex-wrap: wrap;
    }

    #flashcardbtnhang  {
        width: 70px;
    }

    #progresshang h6  {
        font-size: 12px;
    }

    #flashcontenthang h1 {
       font-size: 30px;
    }

    #datadivhang  div div h3 {
        font-size: 20px;
    }

    #hangheader {
        font-size: 12px;
     }

     #datadivhang  div div h4 {
        font-size: 15px;
    }
    
  }

  @media only screen and (max-width:1000px) and (min-width: 0px)  {

    #flashcardcontainerhang{
        margin-right: 0px;
        margin-left: 0px;
    }
    
  }


